import { Component, OnInit } from '@angular/core';
import { PaginationService } from 'src/app/service/pagination.service';
import { TextField, ActionField } from 'src/app/common-module/table-list/list-field-type';
import { ProductConfigService } from 'src/app/service/product-config.service';

@Component({
  selector: 'app-product-config-list',
  templateUrl: './product-config-list.component.html'
})
export class ProductConfigListComponent implements OnInit {

  PageTitle = "Manage Product Congiguration";

  action: any;
  loadform = false;
  loadingState = true;
  objectArray: Array<any> = [];
  pagination: any = null;
  searchText: any = null;
  currentPage: any = 1;
  permissionObject: any = null;
  showPagination: boolean = false;
  //For Dynamic List
  headerData: Array<any> = [];

  constructor(
    private paginationService: PaginationService,
    private productconfigService: ProductConfigService,
  ) { }

  ngOnInit() {
    // this.dataService.permission.subscribe((perms) => {
    //   this.permissionObject = perms['self'];
    // });
    this.loadform = false;
    this.setHeaderData();
    this.getObjects();
  }
  setHeaderData() {
    this.headerData.push(
      new TextField({
        label: "Order Number",
        key: "order.po_no",
      }),
      new TextField({
        label: "Product",
        key: "product.label",
      }),
      new TextField({
        label: "Registration Type",
        key: "registration_type.label",
      }),
      new TextField({
        label: "Serial",
        key: "serial.label",
      })
    );
    let canEdit = true;
    let canDelete = true;
    let actions = {};
    if (canEdit) {
      actions['edit'] = {
        path: '/inbound/product-config/edit/',
        id: 'product_register_config_id'
      }
    }
    if (canDelete) {
      actions['delete'] = {
        path: 'product-config/',
        id: 'product_register_config_id'
      }
    }
    let actionColom = new ActionField(actions);
    this.headerData.push(
      actionColom
    );
  }

  getObjects() {
    const params: any = { page: this.currentPage };
    if (this.searchText) {
      params.search = this.searchText;
    }
    this.productconfigService.getProductConfigList(params).subscribe((response) => {
      this.loadingState = false;
      if (response.success) {

        this.objectArray = response.data.list ? response.data.list : [];
        this.showPagination = true;
        this.pagination = this.paginationService.getPager(response.data.pagination['total_page'], this.currentPage);
      } else {
        this.objectArray = [];
        this.pagination = null;
      }
    }, (error) => {
      this.loadingState = false;
      this.objectArray = [];
      this.pagination = null;
    });
  }

  getPage(page: number) {
    this.currentPage = page;
    this.getObjects();
  }

  searchObject(text) {
    this.searchText = text;
    this.currentPage = 1;
    this.getObjects();
  }
  onReloadEvent() {
    this.getObjects();
  }
}




