import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';
import { SearchModule } from 'src/app/common-module/search/search.module';
import { BtnAddModule } from 'src/app/common-module/btn-add/btn-add.module';
import { TableListModule } from 'src/app/common-module/table-list/table-list.module';
import { PaginationModule } from 'src/app/common-module/pagination/pagination.module';
import { SharedModule } from 'src/app/shared/shared.module';
import { ProductConfigListComponent } from './product-config-list.component';
import { ProductConfigService } from 'src/app/service/product-config.service';

const routes: Routes = [
  {
    path: '',
    component: ProductConfigListComponent,
    data: { title: 'product_config_list' },
  },
  {
    path: 'add',
    loadChildren: () => import('../product-config-add/product-config-add.module').then(m => m.ProductConfigAddModule),
    data: { title: 'add_product_config' },
  },
  {
    path: 'edit/:id',
    loadChildren: () => import('../product-config-add/product-config-add.module').then(m => m.ProductConfigAddModule),
    data: { title: 'edit_product_config' },
  }

]


@NgModule({
  declarations: [ProductConfigListComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    TableListModule,
    SearchModule,
    BtnAddModule,
    PaginationModule,
    SharedModule,
  ], providers: [ProductConfigService]
})

export class ProductConfigListModule { }